@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fredoka+One);
body {
  box-sizing: border-box;
  background: #9e9e9e;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@font-face {
  font-family: 'pokemon-red';
  src: url(https://cdn.rawgit.com/shelune/pokedex-entry/master/pkm-red-font.ttf) format("truetype"); }

/* @import url(./assets/magnifier.svg); */
.App {
  text-align: center;
  background: #9e9e9e; }

* {
  margin: inherit;
  box-sizing: inherit; }

h1 {
  font-family: 'pokemon-red';
  font-size: 40px; }

h2 {
  font-family: 'pokemon-red';
  font-size: 30px;
  margin: 0;
  padding: 20px; }

h3 {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 2.5rem;
  padding: 10px; }

.logo {
  padding: 10px;
  max-width: 50%;
  z-index: inherit; }

.fixed-header {
  position: fixed;
  z-index: 3;
  top: 0;
  padding-bottom: 5px;
  background-color: #9e9e9e;
  max-width: 100%;
  -webkit-transition: height ease-in 0.2s;
  transition: height ease-in 0.2s; }

.input {
  width: 300px;
  z-index: inherit;
  height: 35px;
  border-radius: 20px;
  outline: none;
  padding: 0px 13px;
  border: solid #9e9e9e 2px; }
  .input::-webkit-input-placeholder {
    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
    position: relative;
    background-image: url(/pokedex/static/media/magnifier.9ec29e2e.svg);
    background-repeat: no-repeat;
    background-position: 3px 50%;
    line-height: normal;
    background-size: 13px;
    letter-spacing: 1px;
    padding: 0 25px; }
  .input::-moz-placeholder {
    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
    position: relative;
    background-image: url(/pokedex/static/media/magnifier.9ec29e2e.svg);
    background-repeat: no-repeat;
    background-position: 3px 50%;
    line-height: normal;
    background-size: 13px;
    letter-spacing: 1px;
    padding: 0 25px; }
  .input:-ms-input-placeholder {
    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
    position: relative;
    background-image: url(/pokedex/static/media/magnifier.9ec29e2e.svg);
    background-repeat: no-repeat;
    background-position: 3px 50%;
    line-height: normal;
    background-size: 13px;
    letter-spacing: 1px;
    padding: 0 25px; }
  .input::-ms-input-placeholder {
    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
    position: relative;
    background-image: url(/pokedex/static/media/magnifier.9ec29e2e.svg);
    background-repeat: no-repeat;
    background-position: 3px 50%;
    line-height: normal;
    background-size: 13px;
    letter-spacing: 1px;
    padding: 0 25px; }
  .input::placeholder {
    font-family: 'Courier New', Courier, monospace;
    font-size: 1rem;
    position: relative;
    background-image: url(/pokedex/static/media/magnifier.9ec29e2e.svg);
    background-repeat: no-repeat;
    background-position: 3px 50%;
    line-height: normal;
    background-size: 13px;
    letter-spacing: 1px;
    padding: 0 25px; }

.fa-chevron-right {
  padding: 5px 6px 5px 10px;
  border-radius: 15px;
  position: relative;
  top: 2px;
  left: 10px;
  -webkit-transition: -webkit-transform ease-in 0.1s;
  transition: -webkit-transform ease-in 0.1s;
  transition: transform ease-in 0.1s;
  transition: transform ease-in 0.1s, -webkit-transform ease-in 0.1s; }
  .fa-chevron-right:hover {
    background: #919191; }

.header-wrapper {
  position: relative;
  background: #9e9e9e; }

.type-block {
  position: fixed;
  background: #9e9e9e;
  top: 145px;
  padding: 10px 0;
  width: 100%;
  z-index: -1;
  -webkit-transition: ease-in-out 0.2s;
  transition: ease-in-out 0.2s;
  -webkit-transform-origin: top;
          transform-origin: top; }

.type-search {
  position: relative;
  display: grid;
  margin: auto;
  grid-template-columns: repeat(8, 1fr);
  grid-column-gap: 5px;
  width: 50%; }
  .type-search > span {
    background: gray;
    margin: 3px;
    font-size: 14px;
    width: 100%; }

.pokemon-grid {
  display: block;
  position: relative;
  -webkit-transition: ease-in-out 0.2s;
  transition: ease-in-out 0.2s;
  background-color: #9e9e9e;
  color: white;
  top: 230px;
  max-width: 100%;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(310px, 1fr));
  place-items: center; }

.pokemon {
  position: relative;
  width: 300px;
  background: #6d6d6d;
  margin: 2.5%;
  padding-bottom: 3%;
  border-radius: 5px; }

.pokemon:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  -webkit-transition: ease-out 0.06s;
  transition: ease-out 0.06s; }

.sprite {
  margin-bottom: -30px;
  height: 200px;
  position: relative;
  bottom: 10px; }

.type-span {
  color: white;
  border-radius: 10px;
  padding: 2px 6px;
  border: #dddddd 2px solid;
  font-size: 20px;
  font-family: 'Fredoka One', cursive; }

.info-block {
  position: fixed;
  top: 24%;
  left: 20%;
  z-index: 3;
  color: white;
  width: 60vw;
  margin: auto;
  background: #616161;
  border-radius: 10px;
  font-family: 'Fira Sans', sans-serif;
  box-shadow: 2px 6px 20px rgba(0, 0, 0, 0.5); }

.close-block {
  position: absolute;
  right: 10px;
  top: 10px;
  border-radius: 40px; }
  .close-block:hover {
    background: #545454; }
  .close-block:active {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transition: ease 0.09s;
    transition: ease 0.09s; }
  .close-block > i {
    font-weight: bold;
    font-size: 20px;
    padding: 4px 7px; }

.pokemon-name {
  margin: 35px 0; }

.info-container {
  display: grid;
  grid-template-columns: 1fr 1.2fr 0.7fr;
  place-items: start; }

.pokemon-height-weight {
  background: #787878;
  font-weight: bold;
  position: relative;
  bottom: 7.5%;
  border-radius: 10px; }
  .pokemon-height-weight > div {
    font-size: 2rem;
    padding: 6px; }

.abilities {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 1%;
  font-size: 2rem;
  background: #787878;
  border-radius: 10px; }
  .abilities > div {
    font-weight: bold;
    font-size: 1.7rem;
    text-transform: capitalize;
    background: #555555;
    box-shadow: darkred 2px;
    box-shadow: inset 0 2px 9px rgba(255, 255, 255, 0), inset 0 -2px 6px rgba(0, 0, 0, 0.2);
    border-radius: 30px;
    padding: 5px 15px;
    margin: 5px 20px; }

.stats-block {
  background: #787878;
  padding: 10px;
  border-radius: 10px;
  position: relative;
  right: 10px;
  bottom: 3.5%;
  text-align: center;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 6px; }

.stat-bar {
  position: relative;
  height: 25px;
  margin: 0;
  background: #a3a7aa;
  width: 360px;
  border-radius: 60px; }
  .stat-bar > span {
    display: block;
    height: 100%;
    box-shadow: inset 0 2px 9px rgba(255, 255, 255, 0.3), inset 0 -2px 6px rgba(0, 0, 0, 0.4);
    position: relative;
    overflow: hidden;
    border-radius: inherit; }

.stat-name {
  font-size: 1.3rem;
  text-transform: capitalize; }

.sprite-info {
  position: relative;
  height: 350px;
  bottom: 3%;
  background: #787878;
  border-radius: 10px;
  margin-left: 10px; }

.pokemon-description {
  background: #787878;
  margin: 10px;
  border-radius: 10px;
  font-size: 35px;
  padding: 20px 30px;
  text-align: center; }

@media (orientation: portrait) {
  .info-block {
    height: 45vh;
    width: 96vw;
    left: 2%;
    top: 30%; } }

.my-node-enter {
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left; }

.my-node-enter-active {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: cubic-bezier(0.38, 0.79, 0.21, 1.01) 1s;
  transition: cubic-bezier(0.38, 0.79, 0.21, 1.01) 1s; }

.my-node-exit {
  opacity: 1; }

.my-node-exit-active {
  opacity: 0;
  -webkit-transition: opacity 5000ms;
  transition: opacity 5000ms; }

